import { Router } from "express";
import { list } from "./list";
import { get } from "./get";
import { getXls } from "./get-xls";

export const router = Router();
export { list, get };

router.get("/", (req, res, next) =>
  list(req.query)
    .then(data => res.json(data))
    .catch(next)
);

router.get("/:name", (req, res, next) =>
  get(req.params)
    .then(data => res.json(data))
    .catch(next)
);

router.get("/:name/xls", (req, res, next) => getXls(req.params, res).catch(next));
